.logo {
  max-width: 100px;
}

/*  Intro section  */

.intro {
  position: relative;
}

.intro__img {
  box-shadow: var(--bs);
}

.section__subtitle--intro {
  display: inline-block;
}

@media screen and (min-width: 600px) {
  .intro {
      display: grid;
      width: min-content;
      margin: 0 auto;
      grid-column-gap: 1em;
      grid-template-areas: 
          "img title"
          "img subtitle";
      grid-template-columns: min-content max-content;
  }
  
  .intro__img {
      grid-area: img;
      min-width: 250px;
      position: relative;
      z-index: 2;
  }    
  
  .section__subtitle--intro {
      align-self: start;
      grid-column: -1 / 1;
      grid-row: 2;
      text-align: right;
      position: relative;
      left: -1.5em;
      width: calc(100% + 1.5em);
  }

}


/*  My services section  */

.my-services {
  background-color: var(--clr-dark);
  /* background-image: url(../img/services-bg.jpg); */
  background-size: cover;
  /* background-blend-mode: multiply; */
  color: var(--clr-light);
  text-align: center;
}

.section__title--services {
  color: var(--clr-accent);
  position: relative;
}

.section__title--services::after {
  content: '';
  display: block;
  width: 2em;
  height: 1px;
  margin: 0.5em auto 1em;
  background: var(--clr-light);
  opacity: 0.25;
}

.services {
  margin-bottom: 4em;
}

.service {
  max-width: 500px;
  margin: 0 auto;
}

@media screen and (min-width: 800px) {
  .services {
      display: flex;
      max-width: 1000px;
      margin-left: auto;
      margin-right: auto;
  }
  
  .service + .service {
      margin-left: 2em;
  }
}
/* About me section */

.about-me {
  max-width: 1000px;
  margin: 0 auto;
}

.about-me__img {
  box-shadow: var(--bs);
}

@media screen and (min-width: 600px) {
  .about-me {
      display: grid;
      grid-template-columns: 1fr 200px;
      grid-template-areas: 
          "title img"
          "subtitle img"
          "text img";
      grid-column-gap: 2em;
  }
  
  .section__title--about {
      grid-area: title;
  }
  
  .section__subtitle--about {
      grid-column: 1 / -1;
      grid-row: 2;
      position: relative;
      left: -1em;
      width: calc(100% + 2em);
      padding-left: 1em;
      padding-right: calc(200px + 4em);
  }
  
  .about-me__img {
      grid-area: img;
      position: relative;
      z-index: 2;
  }
}

/* My Work */

.my-work {
  background-color: var(--clr-dark);
  color: var(--clr-light);
  text-align: center;    
}

.portfolio {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.portfolio__item {
  background: var(--clr-accent);
  overflow: hidden;
}
.portfolio1 {
  /* background-image: url(images/Screenshot-for-first-prjt.png); */
  background-size: cover;
  background-position: bottom;
  min-height: 250px;
  width: 30%;
  text-shadow: 0px 2px 2px rgba(0,0,0, 0.418);
  
}
.portfolio2 {
 /*  background-image: url(images/Screenshot-for-portfolio.png); */
  background-size: cover;
  background-position: bottom;
  min-height: 250px;
  width: 30%;
  text-shadow: 0px 2px 2px rgba(0,0,0, 0.418);
  
}
.portfolio3 {
 /*  background-image: url(images/social-media-marketing.jpg); */
  background-size: cover;
  background-position: bottom;
  min-height: 250px;
  width: 30%;
  text-shadow: 0px 2px 2px rgba(0,0,0, 0.418);
}
.portfolios {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.portfolio1 a, .portfolio2 a, .portfolio3 a {
  color: #52c5e1;
  font-size: x-large;
  font-weight: bolder;
  text-shadow: 2px 2px #0c0c0b;
  
}

.portfolio1:focus {
  position: relative;
  z-index: 2;
} 

.portfolio1:hover,
.portfolio1:focus .portfolio__img {
  transform: scale(1.2);
  opacity: .5;
}
.portfolio2:focus {
  position: relative;
  z-index: 2;
} 

.portfolio2:hover,
.portfolio2:focus .portfolio__img {
  transform: scale(1.2);
  opacity: .5;
}
.portfolio3:focus {
  position: relative;
  z-index: 2;
} 

.portfolio3:hover,
.portfolio3:focus .portfolio__img {
  transform: scale(1.2);
  opacity: .5;
}


/* footer */

.footer {
  background: #111;
  color: var(--clr-accent);
  text-align: center;
  padding: 2.5em 0;
  font-size: var(--fs-h3);

}

.footer a { 
  color: inherit;
  text-decoration: none;
}

.footer__link {
  font-weight: var(--fw-bold);
}

.footer__link:hover,
.social-list__link:hover {
  opacity: .7;
}

.footer__link:hover {
  text-decoration: underline;
}

.social-list {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 2em 0 0;
  padding: 0;
}

.social-list__item {
  margin: 0 .5em;
}

.social-list__link {
  padding: .5em;
}


/* Individual portfolio item styles */

.portfolio-item-individual {
  padding: 0 2em 2em;
  max-width: 1000px;
  margin: 0 auto;
}

.portfolio-item-individual p {
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
